
.border-10 {
  border-radius: 10%;
}
.todays-weather {
  border: 1px solid #e5e5e5;
  width: 100%;
  padding: 20px;
  margin: 1em auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-radius: 8px;
}
.weather-display {
  font-size: 3em;
  border: 1px solid black;
  margin-right: 1em;
  padding: 1em;
  border-radius: 8px;
}



